@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #ffffff;
}

* {
  font-family: 'Montserrat', sans-serif;
}

textarea, input {
  outline-color: #ff267c;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  font-size: 21px;
}

button:focus {
  outline: none;
}

.center {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto !important;
  margin-right: auto !important;
}

.title-wrapper {
  margin: 30px auto;
}

.title-text {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
  color: #ff267c;
}

h1.title-text {
  font-size: 54px;
  line-height: 1.333;
  letter-spacing: .25rem;
  margin-top: 20;
  margin-bottom: 20;
}

h2.title-text {
  font-size: 36px;
  line-height: 1.333;
  letter-spacing: .25rem;
  margin-top: 20;
  margin-bottom: 20;
}

.product-description {
  text-align: center;
  /* font-family: "Montserrat", sans-serif; */
  font-size: 2.5rem;
  line-height: 1.255;
  font-weight: bold;
  color: black;
  margin-top: 30px;
  margin-bottom: 30px;
}

.legend-container {
  font-size: 2.5rem;
  line-height: 1.255;
  font-weight: bold;
  color: black;
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}

.btn-primary {
  border-radius: 50px;
  margin-top: 15px;
  margin-bottom: 15px;
  background-color: #ff267c;
  border: none;
  color: #111111;
  font-weight: 500;
  font-family: 'Montserrat', sans-serif;
  font-weight: bolder;
  font-size: 24px;
}

#input-text-area {
  width: 85%;
  margin: 30px;
  height: 500px;
  resize: none;
  padding: 10px 15px;
}

#search-box-wrapper {
  margin: 30px;
  border: 1px solid #ff267c;
  padding: 5px 10px;
  border-radius: 25px;
  width: 95%;
  height: 40px;
}

#search-box-wrapper:hover {
  background-color: #ffe6f0;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}

#search-box-wrapper > input, button {
  background-color: transparent;
  border: none;
  outline: none;
}

#search-box-wrapper > input {
  width: 90%;
  height: 100%;
}

#search-box-wrapper > button {
  width: 10%;
  height: 100%;
}

#nav-bar-wrapper {
  width: 100%;
  background-color: #111111;
  height: 100%;
}

#nav-bar-list {
  display: inline-block;
  margin: 0px;
}

#logo {
  display: inline-block;
  max-height: 20px;
  margin: 10px 20px;
}

.nav-bar-item {
  display: inline-block;
  height: 100%;
  margin: 0px 0px;
  padding: 0px;
}

.nav-bar-item > a {
  display: inline-block;
  font-family: 'Montserrat', sans-serif;
  padding: 15px 20px;
  font-size: 22px;
  font-weight: bold;
  color: #ff267c;
  text-decoration: none;
}

.nav-bar-item:hover a {
  background-color:  #ff267c;
}

.nav-bar-item:hover a {
  color: #111111;
}

.nav-bar-item > a {
  color: #ff267c;
}


span {
    font-size: 21px;
    padding: 7px;
}

#highlighted-text-output {
  line-height: 50px;
}

.PERSON {
  background-color: #4DD0E1;
  /* padding: 7px; */
}

